.logo {
  background-repeat: no-repeat;
  position: absolute;
  height: 50vh;
  width: 100%;
  align-items: center;
  text-align: center;
}

.logo img {
  height: 100%;
}

@media only screen and (min-width: 600px) {
  /* For tablets: */
  .bg {
    /* background-image: url('../assets/image/bg_web_mob.PNG'); */
    background-color: black;
    background-repeat: no-repeat;
    /* min-width: 100vw; */
    min-height: 100vh;
  }
  .logo {
    background-repeat: no-repeat;
    position: absolute;
    height: 30vh;
    width: 100%;
    align-items: center;
    text-align: center;
  }
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .bg {
    background-image: url('../assets/image/bg_web.png');
    background-repeat: no-repeat;
    /* min-width: 100vw; */
    min-height: 100vh;
  }
}
