.services {
  display: flex;
  justify-content: space-between;
  padding-right: 50px;
  padding-left: 50px;

  min-height: 18vh;
  padding-top: 50vh;
}

.services .item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  color: white;
  font-weight: 400;
}

.line {
  border-right: 2px solid white;
}

.chat_us {
  color: #fff;
  border: 1px solid #fff;
  width: 10%;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 45%;
}

@media only screen and (min-width: 600px) {
  /* For Tablets & mobile Phones */
  .services {
    display: flex;
    justify-content: space-between;
    padding-right: 20px;
    padding-left: 20px;
    min-height: 10vh;
    padding-top: 50vh;
  }

  .services .item {
    font-size: 1.4rem;
    font-weight: 500;
  }

  .line {
    border-right: 2px solid white;
  }

  .chat_us {
    width: 20%;
    left: 38%;
    bottom: 10%;
  }
}

@media only screen and (min-width: 768px) {
  /* For Desktops */
}
